import React from 'react'
import PropTypes from 'prop-types'

function Text (props) {
  const style = {
    fontSize: props.fontSize,
    fontFamily: props.fontFamily,
    color: props.color,
    lineHeight: props.lineHeight,
    textAlign: props.textAlign,
    fontWeight: props.fontWeight,
    ...props.style
  }
  // 支持 value 富文本， 如果传了 children，优先使用 children
  let dangerouslySetInnerHTML = (props.value && !props.children) ? {__html: props.value} : undefined
  return (
    <div className={props.className} style={style} dangerouslySetInnerHTML={dangerouslySetInnerHTML}>
      {
        props.children
      }
    </div>
  )
}

Text.defaultProps = {
  value: '请输入文本',
  fontSize: 16,
  fontFamily: undefined,
  color: 'rgba(0,0,0,0.85)',
  lineHeight: 1.15,
  textAlign: 'left',
  fontWeight: 'normal',
  style: {}
}

Text.propTypes = {
  value: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired
}

export default Text
